@use '@styles/colors' as colors;

.OutputInfo {
    flex-grow: 1;
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 12px;
    text-align: center;
}

.primary {
    font-weight: 600;
    font-size: 1.0625rem;
    color: colors.$TEXT_PRIMARY;
}

.secondary {
    color: colors.$TEXT_TETRIARY;
}
